class HelpIdHisto {
	histo: IHelpFrag[] = [];
	currentOffset: number = 0;

	protected recurs = false;

	get currentFrag(): IHelpFrag {return this.histo[this.currentOffset]}

	hasBack() {return this.currentOffset > 0}

	hasForward() {return this.currentOffset < this.histo.length - 1}

	setCurrent(frag: IHelpFrag | null) {
		if (!frag || this.recurs) return;
		if (this.currentOffset < this.histo.length - 1) {
			//On replace la pile remontée
			const after = this.histo.slice(this.currentOffset);
			for (let i = after.length - 1; i >= 0; i--) {
				this.histo[this.currentOffset++] = after[i];
			}
		}
		this.histo.push(frag);
		if (this.histo.length > 50) this.histo.splice(0, 10); //on réduit la liste.
		this.currentOffset = this.histo.length - 1;
	}

	goBack(view: HelpViews) {
		if (this.histo.length > 0 && this.currentOffset > 0) {
			this.recurs = true;
			try {
				view.showLiveHelp(this.histo[this.currentOffset - 1]);
				this.currentOffset--;
				view.refresh();
			} finally {
				this.recurs = false
			}
		}
	}

	goForward(view: HelpViews) {
		if (this.currentOffset < this.histo.length - 1) {
			this.recurs = true;
			try {
				view.showLiveHelp(this.histo[this.currentOffset + 1]);
				this.currentOffset++;
				view.refresh();
			} finally {
				this.recurs = false
			}
		}
	}
}

let liveActions: IAction<HelpViews>[];

function getLiveActions(): IAction<HelpViews>[] {
	if (liveActions) return liveActions;

	const liveSyncLock = new Action<HelpViews>("liveSyncLock").setLabel("Bloquer la synchronisation de l'aide contextuelle avec la sélection courante").setIcon("/@skin@/help/liveSyncLock.svg");
	liveSyncLock.isToggle = () => true;
	liveSyncLock.getDatas = (api: 'toggle', ctx: HelpViews): boolean | null => ctx.isLiveSyncLocked();
	liveSyncLock.execute = (ctx: HelpViews) => ctx.setLiveSyncLocked(!ctx.isLiveSyncLocked());

	const liveBackHisto = new Action<HelpViews>("livePrev").setLabel("Aide précédente").setIcon("/@skin@/help/back.svg");
	liveBackHisto.isEnabled = (ctx: HelpViews) => ctx.liveHisto.hasBack();
	liveBackHisto.execute = (ctx: HelpViews) => ctx.liveHisto.goBack(ctx);

	const liveNextHisto = new Action<HelpViews>("liveForward").setLabel("Aide suivante").setIcon("/@skin@/help/back.svg");
	liveNextHisto.isEnabled = (ctx: HelpViews) => ctx.liveHisto.hasForward();
	liveNextHisto.execute = (ctx: HelpViews) => ctx.liveHisto.goForward(ctx);

	return liveActions = [liveSyncLock, liveBackHisto, liveNextHisto];
}

class GridDataHelpOutline<D> extends MxGridTreeUntouchedDatas(GridDataHolderJsonTree)<D> {
	constructor() {super("members")}
}

class HelpSearchCellBuilder implements ICellBuilder, IHelpPageTplContext {
	reg: IReg<unknown>;

	constructor(public helpDb: IHelpDb) {}

	redrawCell(row: GridDataRowJson<IHelpSearchResult>, root: HTMLElement): void {
		const frag = row.rowKey.found;
		if (frag.isTopic()) {
			root.className = frag.cls || "tag";
			root.replaceChildren(frag.makeTitle(null));
		} else {
			console.trace("TODO rendition help frags without title")
		}
	}

	getColSortFn(): IRowSortFn | null {return null}

	async openFragId(fragId: string, ev?: Event): Promise<void> {
		return this.openFrag(await this.helpDb.getFragment(fragId), ev);
	}

	async openFrag(frag: IHelpFrag, ev?: Event): Promise<void> {
		throw new Error("Method not implemented.");
	}
}

class HelpOutlineCellBuilder implements ICellBuilder, IHelpPageTplContext {
	reg: IReg<unknown>;

	constructor(public helpDb: IHelpDb) {}

	redrawCell(row: GridDataRowJson<IHelpFrag | IHelpIndexNode>, root: HTMLElement): void {
		const frag = "frag" in row.rowKey ? row.rowKey.frag : row.rowKey;
		if (frag.isTopic()) {
			root.replaceChildren(frag.makeTitle(null));
		} else {
			console.trace("? should be topic: ", frag)
		}
	}

	getColSortFn(): IRowSortFn | null {return null}

	async openFragId(fragId: string, ev?: Event): Promise<void> {
		return this.openFrag(await this.helpDb.getFragment(fragId), ev);
	}

	async openFrag(frag: IHelpFrag, ev?: Event): Promise<void> {
		throw new Error("Method not implemented.");
	}
}

